import Layout from '../components/Layout';
import content from '../content/home.md';

let { html , attributes:{ title, events } } = content;

export default () => (
    <Layout>
          <h1>{title}</h1>
          <div dangerouslySetInnerHTML={{ __html: html }}/>
          <ul>
              { events.map((event, k) => (
                  <li key={k}>
                    <h2>{event.name}</h2>
                    <p>{event.description}</p>
                  </li>
              ))}
          </ul>
    </Layout>
);
